import { Attachment } from 'mailparser';

export interface MailConfig {
  username: string;
  tenantId?: string;
  clientId?: string;
  clientSecret?: string;
  emlTestMode?: boolean;
}

export interface MailAttachment {
  id: string;
  contentType: string;
  content?: string;
  filename?: string;
  contentBytes?: Buffer;
}

export interface StoredMailAttachment {
  id: string;
  dsn: string;
  contentType: string;
  filename?: string;
}

export interface MailMessage {
  /**
   * Surrogate, monotonic per sync: Graph lastModifiedDateTime as epoch ms.
   * This advances on moves/edits so backfills (e.g. moving old mail to Inbox)
   * are detected on the next run.
   */
  deltaTimestamp: number;
  deltaId: string;
  id: string;
  attachments?: Array<MailAttachment>;
  attachmentsCount?: number;
  storedAttachments?: Array<StoredMailAttachment>;
  headers?: Array<{ key: string; header: string }>;
  headerLines?: Array<{ key: string; line: string }>;
  subject?: string;
  references?: string | Array<string>;
  date?: Date; // display date (uses received/sent), not the sync cursor
  to?: Array<string>;
  from?: Array<string>;
  cc?: Array<string>;
  bcc?: Array<string>;
  messageId?: string; // RFC822 Message-ID (internetMessageId)
  inReplyTo?: string;
  replyTo?: Array<string>;
  text?: string;
  html?: string;
  textAsHtml?: string;
  originalMessageId?: string;
}
